html {
  height: 100%;
  width: 100%;
}

body {
  height: 100%;
  width: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.home {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-family: 'Tangerine', cursive;
  width: 100vw;
  height: 100vh;
}

.home::after {
  content: "";
  background: url(/KreatywneDzieci/static/media/tlo.c070b7bb.jpg) center center fixed;
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0.8;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1; 
}

.home__welcome {
  font-weight: 400;
  -webkit-flex-basis: 90%;
          flex-basis: 90%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: rgb(110, 58, 244);
}

.home__welcome_text {
  font-size: 4.5rem;
  margin-bottom: 1.5rem;
}

.home__under-welcome_text {
  color: black;
  font-size: 2rem;
}

@media (min-width: 769px) {
  .home__welcome_text {
    font-size: 10rem;
  }

  .home__under-welcome_text {
    font-size: 2rem;
  }
}
.side-drawer-toggle-button {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  height: 1.5rem;
  width: 1.5rem;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  box-sizing: border-box;
}

.side-drawer-toggle-button:focus {
  outline: none;
}

.side-drawer-toggle-button__line {
  width: 1.5rem;
  height: 2px;
  background: black;
}
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: rgb(243, 242, 247, 0.3);
  height: 3rem;
}

.navbar__navigation {
  display: -webkit-flex;
  display: flex;
  height: 100%;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 1rem;
}

.navbar__toggle-button {
  margin-left: 0.5rem;
}

.navbar__navigation-items {
  margin-left: auto;
}

.navbar__navigation-items_list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
}

.navbar__navigation-items_list-item {
  padding: 0 0.5rem;
}

@media (max-width: 768px) {
  .navbar__navigation-items {
    display: none;
  }
}

@media (min-width: 769px) {
  .navbar__toggle-button {
    display: none;
  }
}
.footer {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  background: rgb(243, 242, 247, 0.3);
}

.footer__Content {
  color: black;
  padding: 0.25rem;
}

.footer__Content-text {
  margin: auto;
  font-size: 0.75rem;
}

@media (min-width: 813px) {
  .footer__Content-text {
    margin: auto;
    font-size: 1rem;
  }
}
* {
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

